* {
  margin: 0;
  padding: 0;
}

.case {
  padding: 90px 0;
  font-size: 18px;
  line-height: 1.5em;
  color: #83939c;
}
.case h1 {
  font-size: 56px;
  line-height: 1.2em;
  letter-spacing: -0.5px;
  color: #000000;
  margin: 0;
  margin-bottom: 13px;
}
.case h1 span {
  color: #4e616b;
}
.case h2 {
  margin-bottom: 13px;
  font-size: 46px;
  line-height: 1.46em;
  letter-spacing: -0.5px;
  color: #000000;
}

.case-header {
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px;
}
.case-header p {
  font-weight: 600;
  margin-bottom: 20px;
}

.case-header__links {
  display: flex;
}

.case-banner img {
  border-radius: 24px;
}

.case-movie {
  margin-bottom: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 24px;
  background-image: url(../../img/movie-background.png);
  background-color: #2f2a71;
  background-size: cover;
  min-height: 640px;
}
.case-movie .case-movie__phone {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.case-movie .case-movie__phone video {
  border-radius: 34px;
}
.case-movie .case-movie__phone::after {
  position: absolute;
  display: block;
  content: "";
  width: 276px;
  height: 560px;
  background-image: url(../../img/phone.png);
  background-size: contain;
  background-repeat: no-repeat;
  z-index: 2;
}

.case-info {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 35px;
}
@media (max-width: 768px) {
  .case-info {
    grid-template-columns: 1fr;
    gap: 15px;
  }
}

.case-info__card {
  padding: 32px 24px;
  border-radius: 24px;
  background-color: #2f2a71;
  color: #ffffff;
}
@media (max-width: 768px) {
  .case-info__card {
    position: relative;
    padding-left: 102px;
  }
}
@media (max-width: 768px) {
  .case-info__card img {
    position: absolute;
    top: 32px;
    left: 32px;
  }
}
.case-info__card p {
  font-size: 46px;
  line-height: 1.46em;
  letter-spacing: -0.5px;
  font-weight: 700;
  margin-top: 24px;
}
@media screen and (max-width: 1200px) {
  .case-info__card p {
    font-size: 42px;
  }
}
@media screen and (max-width: 991px) {
  .case-info__card p {
    font-size: 28px;
  }
}
@media (max-width: 768px) {
  .case-info__card p {
    margin-top: 0;
  }
}
.case-info__card span {
  font-weight: 600;
}

.case-paragraph {
  margin-bottom: 30px;
}

.case-review {
  padding-top: 60px;
}
.case-review .case-paragraph {
  margin-bottom: 20px;
}

.case-thesis {
  background-color: #f2f1fe;
  padding: 10px 47px 10px;
  border-radius: 24px;
  margin-bottom: 20px;
  position: relative;
  margin-top: 13px;
}

.case-thesis::before {
  position: absolute;
  content: "";
  display: block;
  width: 19px;
  height: 19px;
  border-radius: 50%;
  background-color: #ffffff;
  top: 50%;
  left: 14px;
  transform: translateY(-50%);
}

.case-solutions {
  padding-top: 60px;
}

.case-feature {
  padding-top: 60px;
}
.case-feature .case-thesis {
  margin-bottom: 60px;
}

.case-feature__content {
  padding: 58px 30px 20px;
  margin-bottom: 13px;
  border-left: 3px solid #fc6020;
  box-shadow: 0px 5.24px 35.24px -8.78881px rgba(0, 0, 0, 0.08);
  border-radius: 0px 24px 24px 0px;
  position: relative;
}

.case-feature__text {
  width: 76%;
}
.case-feature__text blockquote {
  margin-bottom: 20px;
}
.case-feature__text blockquote + p {
  color: #000000;
  width: fit-content;
  margin-left: auto;
  font-size: 14px;
  line-height: 1.43em;
}
.case-feature__text blockquote + p span {
  color: #b5b5b5;
}

.case-feature__developer {
  position: absolute;
  bottom: 0;
  right: 0;
}

.case-process {
  padding-top: 60px;
}
.case-process .case-thesis {
  margin-bottom: 60px;
}
@media screen and (max-width: 991px) {
  .case-process .case-thesis {
    margin-top: 35px;
  }
}

.case-comment {
  padding-top: 60px;
}

.case-comment__content {
  position: relative;
  overflow: hidden;
  display: grid;
  grid-template-columns: auto auto;
  padding: 20px 29px 20px 0;
  margin-bottom: 60px;
  color: #000000;
  border-radius: 24px;
  box-shadow: 0 5.24px 35.24px -8.8px rgba(0, 0, 0, 0.08);
}

.case-comment__content::before {
  position: absolute;
  content: "";
  display: block;
  background-image: url(../../img/svg/icon-quotes.svg);
  width: 181px;
  height: 141px;
  top: -22px;
  left: 0;
}

.case-comment__left {
  transform: translateY(20px);
  align-self: self-end;
}

.case-comment__right blockquote {
  margin-bottom: 36px;
}

.case-comment__person-info {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  line-height: 20px;
}

.case-team {
  padding-top: 60px;
}

.case-team__specialists {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: 30px 30px 20px;
  list-style-type: none;
  box-shadow: 0px 5.24px 35.24px -8.8px rgba(0, 0, 0, 0.08);
  border-radius: 25px;
}
@media (max-width: 768px) {
  .case-team__specialists {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}

.case-team__specialist {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.case-team__specialist p {
  font-weight: 500;
  margin-top: 20px;
  color: #000000;
  text-align: center;
}
.case-team__specialist span {
  font-weight: 300;
  color: #83939c;
}

.case-safety {
  padding-top: 60px;
}

.case-results {
  padding-top: 60px;
}
.case-results ol {
  list-style-type: none;
  margin-bottom: 38px;
}
.case-results ol span {
  font-weight: 600;
}

.case-technologies {
  margin-top: 60px;
  background-image: url(../../img/technologies-background.svg);
  background-size: cover;
  background-position: right;
  padding: 48px 55px;
  color: #ffffff;
  border-radius: 24px;
}
.case-technologies h2 {
  color: #ffffff;
  font-size: 40px;
  line-height: 1.2em;
}
.case-technologies p {
  max-width: 375px;
  margin-bottom: 16px;
}
.case-technologies ul {
  display: flex;
  gap: 24px;
  list-style-type: none;
  font-weight: 600;
}
.case-technologies ul li {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.case-technologies ul span {
  display: block;
  margin-top: 6px;
}

.process-slider .slick-list {
  overflow: visible;
}
@media screen and (max-width: 991px) {
  .process-slider .slick-list {
    overflow: hidden;
  }
}
.process-slider .slick-track {
  counter-reset: card-counter;
  /* Сбрасываем счетчик перед карточками */
  display: flex;
}
@media screen and (max-width: 991px) {
  .process-slider .slick-track {
    display: block;
  }
}

.process-slider__slide {
  position: relative;
  height: auto;
  padding: 80px 32px 20px;
  box-shadow: 0px 5.24px 35.24px -8.8px rgba(0, 0, 0, 0.08);
  border-radius: 15px;
  color: #000000;
}
@media screen and (max-width: 991px) {
  .process-slider__slide {
    height: unset;
  }
}
.process-slider__slide h3 {
  font-size: 24px;
  line-height: 1.25em;
  font-weight: 500;
  margin-bottom: 24px;
}

.process-slider__slide::before {
  content: counter(card-counter, upper-roman);
  /* Используем римские цифры для нумерации */
  counter-increment: card-counter;
  /* Увеличиваем счетчик на 1 для каждой карточки */
  color: #fc6020;
  font-size: 32px;
  line-height: 1.25em;
  position: absolute;
  left: 32px;
  top: 32px;
}

.process-slider__slide + .process-slider__slide {
  margin-left: 20px;
}
@media screen and (max-width: 991px) {
  .process-slider__slide + .process-slider__slide {
    margin-top: 20px;
    margin-left: 0;
  }
}

.slick-prev,
.slick-next {
  transform: rotateZ(90deg);
  width: 40px;
  height: 40px;
  right: -10%;
}

.slick-prev {
  left: unset;
  top: 40%;
}

.slick-next {
  top: 60%;
  transform: rotateZ(90deg);
}

.slick-prev:before,
.slick-next:before {
  color: #fc6020;
  font-size: 40px;
}

